* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.pageContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
}

.imageContainer {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fullImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

@media (max-aspect-ratio: 16/9) {
    .fullImage {
        width: auto;
        height: 100%;
    }
}

@media (min-aspect-ratio: 16/9) {
    .fullImage {
        width: 100%;
        height: auto;
    }
}

/* Estilo base para todas as áreas clicáveis */
.instagramLink {
    position: absolute;
    width: 8vw;
    height: 8vw;
    max-width: 80px;
    max-height: 80px;
    min-width: 40px;
    min-height: 40px;
    cursor: pointer;
    z-index: 10;
}

/* Primeira área clicável */
.link1 {
    left: 20.1%;
    top: 11.2%;
}

/* Segunda área clicável */
.link2 {
    left: 44.4%;
    top: 11.7%;
}

/* Terceira área clicável */
.link3 {
    left: 21.1%;
    top: 18.3%;
    width: 6vw;
    height: 6vw;
    max-width: 70px;
    max-height: 60px;
    min-width: 30px;
    min-height: 30px;
}

/* Quarta área clicável */
.link4 {
    left: 46.5%;
    top: 18.2%;
    width: 6vw;
    height: 6vw;
    max-width: 60px;
    max-height: 60px;
    min-width: 30px;
    min-height: 30px;
}

@media (max-width: 768px) {
    .instagramLink {
        width: 12vw;
        height: 12vw;
    }
    .link3 {
        width: 16vw;
        height: 7vw;
    }

    .link4 {
        width: 28vw;
        height: 8vw;
        max-width: 115px;
    }
}

.clickableArea {
    display: block;
    width: 100%;
    height: 100%;
    background-color: transparent;
}
